.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  stroke: currentColor;
  stroke-width: 0;
}

.icon-youtube {
  font-size: 4.8rem;
  width: 1.416666666666667em;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html, body, .page {
  height: 100%;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", "Calibri", "Verdana", sans-serif;
  font-size: 1.6rem;
  min-width: 32rem;
  line-height: 1.2;
  overflow-x: hidden;
}

body.popup-open {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  background-color: transparent;
  border: none;
}

img {
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

.page {
  display: flex;
  flex-direction: column;
}

.container {
  flex-grow: 1;
}

.wrapper, .full {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  height: 100%;
  position: relative;
}

.full {
  max-width: 192rem;
}

.no-event {
  pointer-events: none;
  cursor: no-drop;
  opacity: 0.5;
}

.no-scroll {
  overflow: -moz-scrollbars-none;
}

.no-scroll::-webkit-scrollbar {
  width: 0;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

#svg-sprite {
  position: absolute;
  width: 0;
  height: 0;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
}

.grid__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
}

.grid__item--1 {
  width: 2.166666666666667%;
}

.grid__item--2 {
  width: 6.333333333333334%;
}

.grid__item--3 {
