}

.relation {
  position: relative;
}

.relation__ratio {
  height: 0;
}

.relation__ratio--16-9 {
  padding-top: 56.25%;
}

.relation__ratio--1-1 {
  padding-top: 100%;
}

.relation__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 0;
}

.youtube {
  position: relative;
}

.youtube__ratio {
  padding-top: 56.25%;
  height: 0;
}

.youtube__iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 0;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.youtube__play {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 68px;
  height: 48px;
  margin-left: -34px;
  margin-top: -24px;
  cursor: pointer;
  border: 0;
  background-color: transparent;
  padding: 0;
  opacity: 0.8;
  filter: "alpha(opacity=80)";
}

.youtube__play:focus {
  outline: 0;
  opacity: 1;
  filter: "alpha(opacity=100)";
}

.youtube__play:hover .ytp-large-play-button-bg {
  transition: fill 0.1s cubic-bezier(0, 0, 0.2, 1), fill-opacity 0.1s cubic-bezier(0, 0, 0.2, 1);
  fill: #FF0000;
  fill-opacity: 1;
}

.youtube__play svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.youtube__play .ytp-large-play-button-bg {
  transition: fill 0.1s cubic-bezier(0.4, 0, 1, 1), fill-opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
  fill: #212121;
  fill-opacity: 0.8;
}

#youtube path:first-child {
  fill: #212121;
}

#youtube path:first-child:hover {
  fill: #FF0000;
}

#youtube path:last-child {
  fill: #FFFFFF;
}

@media screen and (max-width: 768px) {
  .wrapper, .full {
    padding-left: 15px;
    padding-right: 15px;
  }
}
